@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,500&display=swap);
.img-container {
  margin: 0;
  padding: 0;
  position: relative;
  height: 500px;
  overflow: hidden;
  font-size: 0.875rem;
  font-weight: 400;
  
}

.details-container {
  display: flex;
  align-items: flex-end;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.55);
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}

.details {
  position: relative;
  width: 100%;
  height: 50px;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.username {
  max-width: 70%;
  text-align: left;
  word-wrap: break-word;
  padding: 0;
  opacity: 0;
  color: white;
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}

.img-container:hover > .details-container {
  opacity: 1;
}

.details-container:hover > .details .username {
  opacity: 1;
}

.profile-image {
  height: 2rem;
  border-radius: 50%;
  margin: 10px 15px 10px 10px;
}

.material-icons {
  position: absolute;
  right: 15px;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  background: rgba(255, 255, 255, 0);
  border-radius: 50%;
  padding: 10px;
  color: white;
  font-size: 1.5rem;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
  cursor: pointer;
}

.material-icons:hover {
  background: rgba(255, 255, 255, 0.25);
}

.username a {
  color: white;
  text-decoration: none;
  margin-right: 5px;
}

.username a:hover {
  text-decoration: underline;
}

.username span {
  display: inline-block;
  margin-right: 5px;
}

/* touchscreen devices */
@media only screen and (max-width: 813px) and (hover: none),
  (min-width: 568px) and (max-height: 375px) and (hover: none),
  (min-width: 1024px) and (max-height: 1366px) and (hover: none) {
  .details-container {
    opacity: 1;
    /* background: rgba(0, 0, 0, 0); */
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.55)), color-stop(10%, rgba(0,0,0,0.4)), color-stop(50%, rgba(0,0,0,0)));
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.55) 0%, rgba(0,0,0,0.4) 10%, rgba(0,0,0,0) 50%);
  }

  .profile-image {
    height: 32px;
    border-radius: 50%;
    margin: 5px 10px 5px 5px;
  }

  .material-icons {
    right: 10px;
  }

  .username {
    opacity: 1;
  }

  .material-icons:hover {
    background: rgba(255, 255, 255, 0);
  }
}


.loading-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 9999;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
}

.circle {
  position: relative;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  box-sizing: border-box;
}

.circle:nth-child(1) {
  background: #1A1A1A;
  -webkit-animation: anim-reverse 1.5s infinite;
          animation: anim-reverse 1.5s infinite;
}

.circle:nth-child(2) {
  border: 2px solid #1A1A1A;
  background: white;
  -webkit-animation: anim 1.5s infinite;
          animation: anim 1.5s infinite;
}

.circle:nth-child(1),
.circle:nth-child(2) {
  -webkit-transition-timing-function: cubic-bezier(0.41, 0.48, 0.99, 0.57);
          transition-timing-function: cubic-bezier(0.41, 0.48, 0.99, 0.57);
}

@-webkit-keyframes anim {
  0% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
  }
  50% {
    -webkit-transform: rotate(0) translateX(-12px);
            transform: rotate(0) translateX(-12px);
  }
  100% {
    -webkit-transform: rotate(0) translateX(0px);
            transform: rotate(0) translateX(0px);
    z-index: -1;
  }
}

@keyframes anim {
  0% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
  }
  50% {
    -webkit-transform: rotate(0) translateX(-12px);
            transform: rotate(0) translateX(-12px);
  }
  100% {
    -webkit-transform: rotate(0) translateX(0px);
            transform: rotate(0) translateX(0px);
    z-index: -1;
  }
}

@-webkit-keyframes anim-reverse {
  0% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
  }
  50% {
    -webkit-transform: rotate(0) translateX(12px);
            transform: rotate(0) translateX(12px);
  }
  100% {
    -webkit-transform: rotate(0) translateX(-1px);
            transform: rotate(0) translateX(-1px);
  }
}

@keyframes anim-reverse {
  0% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
  }
  50% {
    -webkit-transform: rotate(0) translateX(12px);
            transform: rotate(0) translateX(12px);
  }
  100% {
    -webkit-transform: rotate(0) translateX(-1px);
            transform: rotate(0) translateX(-1px);
  }
}

.header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    height: 60px;
    background: rgb(255, 255, 255);
    box-sizing: border-box;
}

.app-logo {
    height: 20px;
    padding: 20px 20px 20px 20px;
}

.header .header-title {
    position: relative;
    margin: 0;
    font-weight: 500;
    opacity: 0.65;
    font-size: 0.8125rem;
}

.headroom {
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    -webkit-transition: box-shadow 0.5s ease;
    transition: box-shadow 0.5s ease;
}

.headroom--unfixed {
    position: relative;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.headroom--scrolled {
    -webkit-transition: -webkit-transform 200ms ease-in-out;
    transition: -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}

.headroom--unpinned {
    position: fixed;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.headroom--pinned {
    position: fixed;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    box-shadow: 0 3px 2px -2px rgba(0, 0, 0, 0.09);
}

.fade-in {
    -webkit-animation: fade-in 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0.3s both;
            animation: fade-in 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0.3s both;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

body {
  background-color: white;
  display: flex;
  justify-content: center;
  text-align: center; 
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  background: rgb(255, 255, 255);
  overflow-y: scroll;
}

h5 {
  margin: 15px 20px 20px 20px;
  font-weight: 500;
}

.App {
  width: calc(100vw - 17px);
}

.container {
  padding: 0 10px 10px 10px;
  max-width: 1500px;
  margin: auto;
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-auto-rows: minmax(350px, auto);
  grid-auto-flow: row;
}

.img-container:nth-child(5n + 1) {
  grid-column: auto / span 2;
}

.img-container > .image {
  width: 100%;
  height: 100%;
  max-height: 500px;
  object-fit: cover;
  background: rgb(233, 233, 233);
}

/* mobile devices */
@media only screen and (max-width: 720px), (max-width: 1024px) {
  body {
    margin: 0;
  }

  .App {
    width: 100vw;
  }

  .container {
    grid-gap: 0;
    padding: 0px;
  }

  .img-container:nth-child(5n + 1) {
    grid-column: auto;
  }
}

/* iPhone 5 / SE */
@media only screen and (max-width: 320px) {
  .container {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-auto-rows: minmax(350px, auto);
    grid-gap: 0;
  }
}

