.header {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    height: 60px;
    background: rgb(255, 255, 255);
    box-sizing: border-box;
}

.app-logo {
    height: 20px;
    padding: 20px 20px 20px 20px;
}

.header .header-title {
    position: relative;
    margin: 0;
    font-weight: 500;
    opacity: 0.65;
    font-size: 0.8125rem;
}

.headroom {
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    transition: box-shadow 0.5s ease;
}

.headroom--unfixed {
    position: relative;
    transform: translateY(0);
}

.headroom--scrolled {
    transition: transform 200ms ease-in-out;
}

.headroom--unpinned {
    position: fixed;
    transform: translateY(-100%);
}

.headroom--pinned {
    position: fixed;
    transform: translateY(0%);
    box-shadow: 0 3px 2px -2px rgba(0, 0, 0, 0.09);
}

.fade-in {
    animation: fade-in 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0.3s both;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
